import React, { Component } from 'react'
import QrReader from 'react-qr-reader'
import "./st_te.scss"
import Audio from "./sounds/alert.mp3"
class Qrread extends Component {
  state = {
    data:{},
    his:{},
    no:-1,
    modalnum:1,
    id:"Error",
    name:"Error",
    date:"Error",
    time:"Error",
    type:"Error",
    img:"Error",
    desc:"Error",
    result: 'No result',
    fdata:{"Content":"NO DATA","Date":"NO DATA","Time_duration":"NO DATA","Desc":"NO DATA","Pass_number":"NO DATA","User_name":"NO DATA","Pass_type":"NO DATA","read":false},
  }
componentDidMount(){
  this.setState({
    data:this.props.data,
    his:this.props.his
  })
}
  handleScan = data => {
    if (data) {
      let reg_num=data.substr(0,15)
      let name=data.substr(16)
      let k=0;
      function myFunction() {
        var d = new Date();
        var h = d.getHours();
        var m = d.getMinutes();
        return [h,m] ;
      }
      let time = myFunction()
      if((time[0]>18 && time[1]>30) || (time[0]<7 && time[1]<30)){
        
        while(k<this.state.data.length){
          if(name===this.state.data[k].User_name && reg_num===this.state.data[k].Register_number ){
            this.setState({fdata:this.state.data[k],no:k,modalnum:2})
            break
          }
          k=k+1;
        }
        this.setState({
          id:this.state.fdata.Pass_number,
          name:this.state.fdata.User_name,
          date:this.state.fdata.Date,
          time:this.state.fdata.Time_duration,
          type:this.state.fdata.Pass_type,
          img:this.state.fdata.user_image,
          desc:this.state.fdata.Desc,
        })
      }else{
        
        while(k<this.state.data.length){
          if(name===this.state.data[k].User_name && reg_num===this.state.data[k].Register_number ){
            this.setState({fdata:this.state.data[k],no:k,modalnum:1})
            break
          }
          k=k+1;
        }
        function formatAMPM(date) {
          var hours = date.getHours();
          var minutes = date.getMinutes();
          var ampm = hours >= 12 ? 'pm' : 'am';
          hours = hours % 12;
          hours = hours ? hours : 12; 
          minutes = minutes < 10 ? '0'+minutes : minutes;
          var strTime = hours + ':' + minutes + ' ' + ampm;
          return strTime;
        }
        this.setState({
          id:this.state.fdata.Pass_number,
          name:this.state.fdata.User_name,
          date:this.state.fdata.Date,
          time:formatAMPM(new Date),
          type:this.state.fdata.Pass_type,
          img:this.state.fdata.user_image,
          desc:this.state.fdata.Desc,
        })
      }
     
      if( document.getElementById("myModal4").style.display = "none"){
        document.getElementById("myModal4").style.display = "block";
        document.getElementById('audio').play()
      }
      this.setState({
        result: data
      })

    }
    
  }
  handleError = err => {
    console.error(err)
  }
  confirm(){
    var array = [...this.state.data],array1=[...this.state.his]; 
    var index = this.state.no;
    if(array[index].authkey === parseInt(document.getElementById("auth_key2").value)){
    function formatAMPM(date) {
      var hours = date.getHours();
      var minutes = date.getMinutes();
      var ampm = hours >= 12 ? 'pm' : 'am';
      hours = hours % 12;
      hours = hours ? hours : 12; 
      minutes = minutes < 10 ? '0'+minutes : minutes;
      var strTime = hours + ':' + minutes + ' ' + ampm;
      return strTime;
    }
    if (index !== -1) {
      array[index]["Check_in"]=formatAMPM(new Date);
    array1.push(array[index])
    array.splice(index, 1);
    this.setState({data: array,his:array1});
    document.getElementById("myModal4").style.display = "none";
    this.cancel()
    document.getElementById("auth_key2").value="";
  }
}else{
  alert('incorrect authentication key');
  document.getElementById("auth_key2").value="";
}
   
  }
  confirm_pass1(){
    
  }
  cancel(){
    this.setState({fdata:{"Content":"NO DATA","Date":"NO DATA","Desc":"NO DATA","Pass_number":"NO DATA","User_name":"NO DATA","read":false}})
    document.getElementById("auth_key2").value="";
  }
  bt(){
    document.getElementById("myModal4").style.display = "none";
    try{
    document.getElementById("auth_key2").value="";
    }catch(err){
      // console.log(err)
    }
  }
  render() {
   
    return (
      <div className="qr">
         <audio id="audio"><source src={Audio} type="audio/mp3"></source></audio>
        <div id="myModal4" className="modal">


{this.state.modalnum===2?
<div className="modal-content">
  <div className="modal-header">
  <h2 style={{color:"white"}}>Student Details</h2>
  <div onClick={this.props.call}><span className="close" id='cls' onClick={this.bt}>&times;</span></div>

  </div>

  <div className="profile">

    <div className="Tag-no">
      <center>Pass Number : {this.state.id}</center>
    </div>
  
    <div className="info-part">
      <div  className="student-image">
     
        <img src={this.state.img} alt='the profile picture' className="pic"/>
      </div>
      <div className="details">
        <div>
        Type:</div>
        <div className="details-answer">{this.state.type}</div>
       <div>Name: </div> 
       <div className="details-answer">{this.state.name}</div>
       <div>Date: </div> 
       <div className="details-answer">{this.state.date}</div> 
       <div>Time Duration: </div>
       <div className="details-answer">{this.state.time}</div>
        <div>Description:</div>
        <div className="details-answer">{this.state.desc}</div>
      
  
      </div>
  
  </div>
  
  
  <div className="submit-part">
    <center>
      <input type="number" placeholder="Enter authentication code" id="auth_key2"/>
      <div onClick={this.props.call}><button type="submit" className="confirm-button" onClick={this.confirm.bind(this)}>Confirm</button></div>
    <div onClick={this.props.call}><button type="submit" className="decline-button" onClick={this.bt}>Decline</button></div>
  </center>
  </div>
  </div> 
</div>:


<div className="modal-content">
  <div className="modal-header">
  <h2 style={{color:"white"}}>Student Details</h2>
  <div onClick={this.props.call}><span className="close" id='cls' onClick={this.bt}>&times;</span></div>

  </div>

  <div className="profile">

    <div className="Tag-no">
      <center>GATE PASS 1</center>
    </div>
  
    <div className="info-part">
      <div  className="student-image">
     
        <img src={this.state.img} alt='the profile picture' className="pic"/>
      </div>
      <div className="details">
        
       <div>Name: </div> 
       <div className="details-answer">{this.state.name}</div>
       <div>Date: </div> 
       <div className="details-answer">{this.state.date}</div> 
       <div>Time: </div>
       <div className="details-answer">{this.state.time}</div>
      
  
      </div>
  
  </div>
  
  
  <div className="submit-part">
    <center>
    <div onClick={this.props.call}><button type="submit" className="confirm-button" onClick={this.confirm_pass1.bind(this)}>Confirm</button></div>
    <div onClick={this.props.call}><button type="submit" className="decline-button" onClick={this.bt}>Close</button></div>
  </center>
  </div>
  </div> 
</div>}
</div>
        <div id="myModal4" className="modal">


<div className="modal-content">
  <div className="modal-header">
  <h2 style={{color:"white"}}>Student Details</h2>
  <span className="close" id='cls' onClick={this.bt}>&times;</span>

  </div>

  <div className="profile">

    <div className="Tag-no">
      <center>Pass Number : {this.state.id}</center>
    </div>
  
    <div className="info-part">
      <div  className="student-image">
     
        <img src={this.state.img} alt='the profile picture' className="pic"/>
      </div>
      <div className="details">
        <div>
        Type:</div>
        <div className="details-answer">{this.state.type}</div>
       <div>Name: </div> 
       <div className="details-answer">{this.state.name}</div>
       <div>Date: </div> 
       <div className="details-answer">{this.state.date}</div> 
       <div>Time Duration: </div>
       <div className="details-answer">{this.state.time}</div>
        <div>Description:</div>
        <div className="details-answer">{this.state.desc}</div>
      
  
      </div>
  
  </div>
  
  
  <div className="submit-part">
    <center>
      <input type="number" placeholder="Enter authentication code"/>
    <button type="submit" className="confirm-button" onClick={this.confirm.bind(this)}>Confirm</button>
    <button type="submit" className="decline-button" onClick={this.bt}>Decline</button>
  </center>
  </div>
  </div> 
</div>
</div>
        <div className="sh">
        <QrReader
          delay={300}
          onError={this.handleError}
          onScan={this.handleScan}
        /></div>
      </div>
      
    )
  }
}

export default Qrread