import { faEye, faPlus, faRetweet, faSearch, faTrash } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React,{Component} from 'react';
import './style_gate.scss'
import Qrread from './qrreader';
class Gate extends Component{
    constructor(props){
      super(props)
      this.state ={
        data:this.props.go.inbox,
        no:-1,
        id:"Error",
        name:"Error",
        date:"Error",
        time:"Error",
        type:"Error",
        img:"Error",
        desc:"Error",
        fdata:{"Content":"NO DATA","Date":"NO DATA","Desc":"NO DATA","Pass_number":"NO DATA","User_name":"NO DATA","read":false},
        fdata2:{"Content":"NO DATA","Date":"NO DATA","Desc":"NO DATA","Pass_number":"NO DATA","User_name":"NO DATA","read":false,"Check_in":"NO DATA","Pass_type":"NO DATA"},
        his:this.props.go.history,
        ctime:"Error",
        message:"some_text",
        page_no:"1"
      }
  this.bt=this.bt.bind(this);
  this.qr_close=this.qr_close.bind(this);
    }
    
    fun(e){
      this.setState({page_no:e})
      } 

      btn(i){
        let array = [...this.state.data];
      array[i-1].read=true;
        this.setState({
          data:array,
          id:this.state.data[i-1].Pass_number,
          name:this.state.data[i-1].User_name,
          date:this.state.data[i-1].Date,
          time:this.state.data[i-1].Time_duration,
          type:this.state.data[i-1].Pass_type,
          img:this.state.data[i-1].user_image,
          desc:this.state.data[i-1].Desc,
          no:i-1
        })
      if( document.getElementById("myModal").style.display = "none"){
        document.getElementById("myModal").style.display = "block";
      }
      }
      bn(){
        this.setState({
          id:this.state.fdata.Pass_number,
          name:this.state.fdata.User_name,
          date:this.state.fdata.Date,
          time:this.state.fdata.Time_duration,
          type:this.state.fdata.Pass_type,
          img:this.state.fdata.user_image,
          desc:this.state.fdata.Desc,
          show:false
        })
      if( document.getElementById("myModal").style.display = "none"){
        document.getElementById("myModal").style.display = "block";
      } 
      }
      btnh(j){
        this.setState({
          id:this.state.his[j-1].Pass_number,
          name:this.state.his[j-1].User_name,
          date:this.state.his[j-1].Date,
          time:this.state.his[j-1].Time_duration,
          type:this.state.his[j-1].Pass_type,
          img:this.state.his[j-1].user_image,
          desc:this.state.his[j-1].Desc,
          ctime:this.state.his[j-1].Check_in
        })
      if( document.getElementById("myModali").style.display = "none"){
        document.getElementById("myModali").style.display = "block";
      }
      } 
      bnh(){
        this.setState({
          id:this.state.fdata2.Pass_number,
          name:this.state.fdata2.User_name,
          date:this.state.fdata2.Date,
          time:this.state.fdata2.Time_duration,
          type:this.state.fdata2.Pass_type,
          img:this.state.fdata2.user_image,
          desc:this.state.fdata2.Desc,
          ctime:this.state.fdata2.Check_in
        })
      if( document.getElementById("myModali").style.display = "none"){
        document.getElementById("myModali").style.display = "block";
      }
      }   
      
      bt(){
        document.getElementById("myModal").style.display = "none";
        document.getElementById("auth_key").value="";
      }
      find(){
        let k=0,a=document.getElementById("inptxt").value;
      if(a!==""&& /^\d+$/.test(a)){
        while(k<this.state.data.length){
          if(parseInt(a)===this.state.data[k].Pass_number){
            this.setState({fdata:this.state.data[k],no:k})
          }
          k=k+1;
        }
        document.getElementById("authe").style.display = "none";
        document.getElementById("fauth").style.display = "table";
        document.getElementById("Cl").style.display = "block";
      }  
      }
      cancel(){
        document.getElementById("authe").style.display = "table";
        document.getElementById("fauth").style.display = "none";
        document.getElementById("Cl").style.display = "none";
        document.getElementById("inptxt").value='';
        this.setState({fdata:{"Content":"NO DATA","Date":"NO DATA","Desc":"NO DATA","Pass_number":"NO DATA","User_name":"NO DATA","read":false}})
      }
      find2(){
        let k=0,a=document.getElementById("inptxt2").value;
      if(a!==""&& /^\d+$/.test(a)){
        while(k<this.state.his.length){
          if(parseInt(a)===this.state.his[k].Pass_number){
            this.setState({fdata2:this.state.his[k]})
          }
          k=k+1;
        }
        document.getElementById("authe2").style.display = "none";
        document.getElementById("fauth2").style.display = "table";
        document.getElementById("C2").style.display = "block";
      }  
      }
      cancel2(){
        document.getElementById("authe2").style.display = "table";
        document.getElementById("fauth2").style.display = "none";
        document.getElementById("C2").style.display = "none";
        document.getElementById("inptxt2").value=''
        this.setState({fdata2:{"Content":"NO DATA","Date":"NO DATA","Desc":"NO DATA","Pass_number":"NO DATA","User_name":"NO DATA","read":false}})
      }
      refresh(){
        alert("no dataset")
        this.setState({data:this.props.go.inbox})
      }
      confirm(){
        var array = [...this.state.data],array1=[...this.state.his]; 
        var index = this.state.no;
        if(array[index].authkey === parseInt(document.getElementById("auth_key").value)){
          function formatAMPM(date) {
            var hours = date.getHours();
            var minutes = date.getMinutes();
            var ampm = hours >= 12 ? 'pm' : 'am';
            hours = hours % 12;
            hours = hours ? hours : 12; 
            minutes = minutes < 10 ? '0'+minutes : minutes;
            var strTime = hours + ':' + minutes + ' ' + ampm;
            return strTime;
          }
          if (index !== -1) {
            array[index]["Check_in"]=formatAMPM(new Date);
          array1.push(array[index])
          array.splice(index, 1);
          this.setState({data: array,his:array1});
          document.getElementById("myModal").style.display = "none";
          this.cancel()
          document.getElementById("auth_key").value="";
        }
  }else{
    alert('incorrect authentication key');
    document.getElementById("auth_key").value="";
  }
      }
      close(){
        document.getElementById("auth_key").value="";
        document.getElementById("myModali").style.display = "none";
      }
      qr(){
        this.setState({
          show:!this.state.show
        })
      }
      qr_close(){
        this.setState({
          show:false
        })
      }
render(){
  let dim = {
    "opacity": 0.8,
    "font-weight": 520,
   " border-bottom": "1px solid #b6b6b6"
  }
  window.onclick = function(event) {
    var modal=document.getElementById("myModal")
    var modal1=document.getElementById("myModali")
    if (event.target == modal) {
      modal.style.display = "none";
    }
    if (event.target == modal1) {
      modal1.style.display = "none";
    }
  }
  let i=0,j=0;
  let sty={"font-weight":"750"};
    return(
        <div className='gte'>
        <div id="myModal" className="modal">


        <div className="modal-content">
          <div className="modal-header">
          <h2 style={{color:"white"}}>Student Details</h2>
          <span className="close" id='cls' onClick={this.bt}>&times;</span>
      
          </div>
      
          <div className="profile">
      
            <div className="Tag-no">
              <center>Pass Number : {this.state.id}</center>
            </div>
          
            <div className="info-part">
              <div  className="student-image">
             
                <img src={this.state.img} alt='the profile picture' className='pic'/>
              </div>
              <div className="details">
                <div>
                Type:</div>
                <div className="details-answer">{this.state.type}</div>
               <div>Name: </div> 
               <div className="details-answer">{this.state.name}</div>
               <div>Date: </div> 
               <div className="details-answer">{this.state.date}</div> 
               <div>Time Duration: </div>
               <div className="details-answer">{this.state.time}</div>
                <div>Description:</div>
                <div className="details-answer">{this.state.desc}</div>
              
          
              </div>
          
          </div>
          
          
          <div className="submit-part">
            <center>
              <input type="number" placeholder="Enter authentication code" id="auth_key"/>
            <button type="submit" className="confirm-button" onClick={this.confirm.bind(this)}>Confirm</button>
            <button type="submit" className="decline-button" onClick={this.bt}>Decline</button>
          </center>
          </div>
          </div> 
        </div>
      </div>
      <div id="myModali" className="modali">


        <div className="modal-content">
          <div className="modal-header">
            
          <h2 style={{color:"white"}}>Student Details</h2>
          <span className="close" id='cls' onClick={this.close.bind(this)}>&times;</span>
    
          </div>
      
          <div className="profile">
      
            <div className="Tag-no">
              <center>Pass Number : {this.state.id}</center>
            </div>
          
            <div className="info-part">
              <div  className="student-image">
             
                <img src={this.state.img} alt='the profile picture' className='pic'/>
              </div>
              <div className="details">
                <div>
                Type:
              </div>
               <div className="details-answer">{this.state.type}</div>
               <div> Name: </div>
               <div className="details-answer">{this.state.name}</div>
                <div>Date:</div>
                <div className="details-answer">{this.state.date}</div>
                <div>Check-in: </div>
                <div className="details-answer">{this.state.ctime}</div>
                <div>Description:</div>
                <div className="details-answer">{this.state.desc}</div>
              
              </div>
          
          </div>
          
          
          <div className="submit-part">
            <center>
            <button type="submit" className="confirm-button" onClick={this.close.bind(this)}>Close</button>
          </center>
          </div>
          </div> 
        </div>
      </div>
      <div className="mbox">                       
      <div  className=" col1">
        <h1>Gate Pass</h1> 
      </div>
      <div className="main-grid">
        <div className="grid0">
          <button type="button" id='N2' className ="sec n2" onClick={this.fun.bind(this,"1")} name='qr' style={this.state.page_no==="1"?null:dim}>QR-Scanner</button>
          <button type="button" id='N1' className ="sec n1" onClick={this.fun.bind(this,"2")} name='not' style={this.state.page_no==="2"?null:dim}>Notification</button>
          <button type="button" id='N3' className ="sec n3" onClick={this.fun.bind(this,"3")} name='his' style={this.state.page_no==="3"?null:dim}>History</button></div>
         
        <div className="grid1">
          {this.state.page_no==="2"?<div id='P1' className="p1" >
        <div className="he">
          <div className="head">
            <div> <h6 style={{paddingTop:"5px"}}><b>Gate pass confirmation</b></h6></div>
            
          </div>
        </div>
  
          <div className="table1 grid3">
            <div  className="intable1">
              <div className="lf">
              <input type="text" placeholder="Search by Id number..." className="form-control" name="search" id="inptxt" style={{height:"100%"}}/>
              <a href="#!" className="sear"><FontAwesomeIcon icon={faSearch}  onClick={this.find.bind(this)} className="fa fa-search"/></a><div>&nbsp;&nbsp;&nbsp;&nbsp;</div>
            
<button type="button" className="btn btn-primary clear btn-sm" id="Cl" onClick={this.cancel.bind(this)}>Clear</button></div>
              <div >  <FontAwesomeIcon icon={faRetweet}  onClick={this.refresh.bind(this)} className="fa fa-search rg"/></div>
            
             
           </div>
           <table  id="hd"><thead>
                  <tr>
                  <th>#</th>
                <th>ID No.</th>
              <th >Name</th>
              <th>Date</th>
               <th style={{color:"#24242c"}}>Vie</th>
                  </tr>
                  </thead>
                  <tbody className='bd'>
                  <tr class ='second'>
              <td>2</td>
              <td>331</td>
              <td>Tony Stark</td>
              <td>12/12/20</td>
              <td> <a href="#"><FontAwesomeIcon icon={faEye} className="ic" aria-hidden="true" /></a></td>
               </tr> 
                  </tbody>
                </table>
                <div className="non">
                <table id="authe">  
                
                <tbody className="nt">
                    {this.state.data.map(dt=>{
                      i=i+1;
                        return(
                <tr style={dt.read?null:sty} key={i}>
                  <td>{i}</td>
                 <td>{dt.Pass_number}</td>
            <td>{dt.User_name}</td>
                 <td >{dt.Date} </td>
                 <td > <a href="#!"><FontAwesomeIcon icon={faEye} className="ic" aria-hidden="true" onClick={this.btn.bind(this,i)} /></a></td>
                  </tr> 
                        )
                    })}
                  </tbody> 
                </table> 
                <table id="fauth">  
                
                <tbody className="nt">
                    {
                <tr >
                  <td>1</td>
                 <td>{this.state.fdata.Pass_number}</td>
            <td>{this.state.fdata.User_name}</td>
                 <td >{this.state.fdata.Date} </td>
                 <td > <a href="#!"><FontAwesomeIcon icon={faEye} className="ic" aria-hidden="true" onClick={this.bn.bind(this)} /></a></td>
                  </tr> 
                        }
                  </tbody> 
                </table> 
                </div>
           </div>  
  
      </div>:this.state.page_no==="1"?  <div id='P2' className="p2" >
      <div className="he">
          <div className="head">
            <div> <h6 style={{paddingTop:"5px"}}><b>Gate pass confirmation</b></h6></div>
            
          </div>
        </div>
        <div className="qr">
          {!this.state.show && <center><img src="data:image/png;base64," alt="qrcode" className="qrc"/>
                <div className="down">
                  <div className="info-q">
                    Scan your QR code to display the gate pass confirmation
                  </div>
                  
                 
                </div>
        </center>}
        {this.state.show && <Qrread call={this.qr_close} data={this.state.data} his={this.state.his}/>}
          
          <span className="dot" ><FontAwesomeIcon icon={faPlus}   className="fa fa-search qrcd" onClick={this.qr.bind(this)}/></span>
          
          </div>
      </div>:this.state.page_no==="3"?<div id='P3' className="p3" >
      <div className="he">
          <div className="head">
            <div> <h6 style={{paddingTop:"5px"}}><b>Gate pass confirmation History</b></h6></div>
          </div>
        </div>
        <div>
        <div className="table1 grid3">
            <div  className="intable1">
              <div className="lf" style={{display:"flex",flexDirection:"row"}}>
              <input type="text" placeholder="Search by Id number..." className="form-control" name="search" id="inptxt2" style={{height:"100%",marginRight:"1vw"}}/>
              <a href="#!" className="sear"><FontAwesomeIcon icon={faSearch}  onClick={this.find2.bind(this)} className="fa fa-search"/></a><div>&nbsp;&nbsp;&nbsp;&nbsp;</div>
              <button type="button" className="btn btn-primary clear btn-sm" id="C2" onClick={this.cancel2.bind(this)}>Clear</button></div>
              
            
             
           </div>
           <table  id="hd" ><thead style={{background:"#24242c",color:"white"}}>
                  <tr>
                  <th>#</th>
                <th>ID No.</th>
              <th >Name</th>
              <th>Date</th>
               <th style={{color:"#24242c"}}>Vie</th>
                  </tr>
                  </thead>
                  <tbody className='bd'>
                  <tr className ='second'>
              <td>2</td>
              <td>331</td>
              <td>Tony Stark</td>
              <td>12/12/20</td>
              <td> <a href="#"><FontAwesomeIcon icon={faEye} className="ic" aria-hidden="true" /></a></td>
               </tr> 
                  </tbody>
                </table>
                <div className="non">
                <table id="authe2">  
                
                <tbody className="nt">
                    {this.state.his.map(dt=>{
                      j=j+1;
                        return(
                <tr >
                  <td>{j}</td>
                 <td>{dt.Pass_number}</td>
            <td>{dt.User_name}</td>
                 <td >{dt.Date} </td>
                 <td > <a href="#!"><FontAwesomeIcon icon={faEye} className="ic" aria-hidden="true" onClick={this.btnh.bind(this,j)} /></a></td>
                  </tr> 
                        )
                    })}
                  </tbody> 
                </table> 
                <table id="fauth2" style={{display:"none"}}>  
                
                <tbody className="nt">
                    {
                <tr >
                  <td>1</td>
                 <td>{this.state.fdata2.Pass_number}</td>
            <td>{this.state.fdata2.User_name}</td>
                 <td >{this.state.fdata2.Date} </td>
                 <td > <a href="#!"><FontAwesomeIcon icon={faEye} className="ic" aria-hidden="true" onClick={this.bnh.bind(this)} /></a></td>
                  </tr> 
                        }
                  </tbody> 
                </table> 
                </div>
           </div>
           </div>
      </div>:null}
        
    
      
    </div>
        <div className="grid2"></div>
      </div>
  </div>
  </div>
 
    )
}
}

export default Gate